
.fm-player{
    width: 680px
}
.header{
  border-radius: 10px 10px 0 0;
  height: 70px;
  background: rgb(5,165,160)
}
.header .left{
  display: inline-block;
  width: 240px;
  padding-left: 20px
}
.header .left .ball{
  width: 20px;
  height: 20px;
  border-radius: 10px;
  display: inline-block;
  margin-left: 10px;
  background: rgba(255,255,255,0.4);
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.2) inset, 0 -2px 2px 2px rgb(11, 152, 156) inset, 0 1px 1px 1px rgba(255,255,255,0.2), 0 -1px 1px 0px rgba(0,0,0,0.2);
  margin-left: 10px
}
.header .mid{
  display: inline-block;
  font-size: 30px;
  color: white;
  width: 200px;
}
.header .right{
  margin-left: 140px;
  margin-top: 20px;
}
.nav{
  background: rgb(57,59,60);
  height: 65px;
}
.container,.leftaside,.rightaside{
  float: left
}
.wrap{
  width: 600px;
  margin: 0 40px;
}
.leftaside,.rightaside{
  width: 40px;
  height: 67px;
  font-size: 30px;
  line-height: 67px;
  text-align: center;
  color: rgb(123,127,124);
}
.leftaside{
  margin-left: -100%
}
.rightaside{
  margin-left: -40px;
}
.nav .wrap div{
  float: left;
  width: 200px;
  line-height: 65px;
  font-size: 35px;
  text-align: center;
  color: white;
}
.nav .wrap .preStyle,.nav .wrap .nextStyle{
  color: rgb(123,127,124);
  font-size: 30px
}
.main{
  padding-top: 30px;
  padding-left: 40px;
  height: 250px;
  background: rgb(232,237,234);
  border-radius: 0 0 10px 10px;
  box-sizing: border-box;
}
.main .cover{
  float: left;
  height: 170px
}
.content{
  float: left;
  width: 360px;
  height: 170px;
  margin-left: 80px;
}
.main h3{
  font-size: 20px;
  color: rgb(123,127,124);
  margin-top: 0;
  margin-bottom: 0
}
.main p,.main .time{
  float: left;
  font-size: 20px;
  margin-top: 0;
  margin-bottom: 0
}
.main .time{
  float: right;
}
.main .song:after{
  display: block;
  content: '';
  clear: both
}
.main .progress-bar{
  margin-top: 20px;
  height: 3px;
  background: rgb(206,217,211);
}
.main .progress-bar .progress{
  height: 100%;
  background: rgb(145,190,180);
}
.main .icon{
  margin-top: 35px
}
.main .icon img{
  float: left;
  width: 46px;
  height: 40px;
}
.main .icon .next{
  float: right;
}
.main .icon .play,.pause{
  margin-left: 110px;
}
.lyric{
  text-align: center;
  margin: 0 auto;
  color: rgb(145,190,180)
}



